import React from 'react';
import Title from './reusable/Title';

function New(props) {
  return (
    <section id='new' className='sectionSpace'>
      <Title type={props.sort}></Title>
      <div className="container">
        <div className="row text-center justify-content-betweeb align-items-center">

          <div className="newbox col-4">
            <div>
              <img className="img-fluid rounded-pill" src={process.env.PUBLIC_URL + '/img/compImg/new2.jpg'} alt="New Product" />
            </div>
            <div className="pt-5">
              <h4>  </h4>
              <p className="descPhr text-muted mb-0">        ,
              <br />   </p>
            </div>
          </div>

          <div className="newbox col-4">
            <div>
              <img className="img-fluid rounded-pill" src={process.env.PUBLIC_URL + '/img/compImg/new2.jpg'} alt="New Product" />
            </div>
            <div className="pt-5">
              <h4>  </h4>
              <p className="descPhr text-muted mb-0">    
              <br />   </p>
            </div>
          </div>

          <div className="newbox col-4">
            <div>
              <img className="img-fluid rounded-pill" src={process.env.PUBLIC_URL + '/img/compImg/new2.jpg'} alt="New Product" />
            </div>
            <div className="pt-5">
              <h4>  </h4>
              <p className="descPhr text-muted mb-0">, <br />  ,<br /> </p>
            </div>
          </div>

        </div>
      </div>
    </section>
  );
}

export default New;